import {
  Component,
  OnInit,
  AfterViewInit,
  ChangeDetectorRef,
  Output,
  EventEmitter
} from "@angular/core";
import { ConfigurationService } from "src/app/services/configuration.service.js";
import { ConfigItem } from "src/app/interfaces/config.js";
@Component({
  selector: "app-panel-pick-color",
  templateUrl: "./panel-pick-color.component.html",
  styleUrls: [
    "./panel-pick-color.component.scss",
    "../preloader/preloader.component.scss"
  ]
})
export class PanelPickColorComponent implements OnInit, AfterViewInit {
  public isOpenPanel = false;
  public radiosCirle = 0.9;
  @Output() changeColor = new EventEmitter();
  @Output() changePosition = new EventEmitter();
  elelement = document.getElementById("colorwheel__container");
  public palettes: Array<string> = [
    "#ff0000",
    "#ff6600",
    "#ff9400",
    "#fec500",
    "#ffff00",
    "#8cc700",
    "#0fad00",
    "#00a3c7",
    "#0064b5",
    "#0010a5",
    "#6300a5",
    "#c5007c"
  ];
  public defaultColors: string[] = [
    "#494a4f",
    "#ffffff",
    "#000105",
    "#3e6158",
    "#3f7a89",
    "#96c582",
    "#b7d5c4",
    "#bcd6e7",
    "#7c90c1",
    "#9d8594",
    "#dad0d8",
    "#4b4fce",
    "#4e0a77",
    "#a367b5",
    "#ee3e6d",
    "#d63d62",
    "#c6a670",
    "#f46600",
    "#cf0500",
    "#efabbd",
    "#8e0622",
    "#f0b89a",
    "#f0ca68",
    "#62382f",
    "#c97545"
  ];
  public colorCurrent;
  constructor(
    private configurationService: ConfigurationService,
    public cd: ChangeDetectorRef
  ) {
    // cd.detach();
  }

  ngOnInit() {
    this.configurationService.configItem$.subscribe((data: string) => {
      this.colorCurrent = data;
    });
  }
  ngAfterViewInit() {
    // ColorWheel();
    // const ColorWhell = new ColorWheel();
    // ColorWhell.init();
  }

  getValuePath(color: string, index: number) {
    const percent = 1 / this.palettes.length;
    const offset = -(percent / 2);
    const r = this.radiosCirle;
    const p1 = offset + index * percent;
    const p2 = offset + (index + 1) * percent;

    const x1 = r * Math.cos(2 * Math.PI * p1);
    const y1 = r * Math.sin(2 * Math.PI * p1);

    const x2 = r * Math.cos(2 * Math.PI * p2);
    const y2 = r * Math.sin(2 * Math.PI * p2);

    const laf = 0;
    return `M ${x1} ${y1} A ${r} ${r} 0 ${laf} 1 ${x2} ${y2} L 0 0`;
  }
  openPanel() {
    this.isOpenPanel = true;
  }
  closePanel() {
    this.isOpenPanel = false;
  }

  setColorWall(item, event) {
    // console.log(event);
    this.changeColor.emit(item);
    this.configurationService.setColor(item);
    this.cd.markForCheck();
    this.cd.detectChanges();
  }
  changePositionCamera(){
    this.changePosition.emit(null);
  }
  ngOnChanges(values) {
    this.cd.detectChanges();
  }
}
