import { Component, OnInit } from "@angular/core";
import { ConfigurationService } from "src/app/services/configuration.service";

@Component({
  selector: "app-preloader",
  templateUrl: "./preloader.component.html",
  styleUrls: ["./preloader.component.scss"]
})
export class PreloaderComponent implements OnInit {
  constructor(private configurationService: ConfigurationService) {}
  public isOpen = true;
  public isOpenPredload = true;

  public percent = 1;
  ngOnInit() {
    this.configurationService.isLoading$.subscribe(data => {
      this.isOpen = data;
    });
    this.configurationService.percentLoading$.subscribe(percent => {
      this.percent = percent;
    });
  }
  checkReady() {
    return this.percent >= 100;
  }
  clickDone() {
    if (this.checkReady()) {
      this.isOpenPredload = false;
    }
    return;
  }
  closekUserGuide() {
    this.configurationService.setIsLoading(false);
  }
}
