import { Injectable, ChangeDetectorRef, EventEmitter } from "@angular/core";
import { BehaviorSubject } from "rxjs";
import { ConfigItem } from "../interfaces/config";

@Injectable({ providedIn: "root" })
export class ConfigurationService {

  changeDetectionEmitter: EventEmitter<void> = new EventEmitter<void>();
  
  private isLoading = new BehaviorSubject<boolean>(true);
  public isLoading$ = this.isLoading.asObservable();


  private percentLoading = new BehaviorSubject<number>(0);
  public percentLoading$ = this.percentLoading.asObservable();

  private totalItem = new BehaviorSubject<number>(3);
  public totalItem$ = this.totalItem.asObservable();

  private loadedItem = new BehaviorSubject<number>(0);
  public loadedItem$ = this.totalItem.asObservable();

  private isOpen = new BehaviorSubject<boolean>(false);
  public isOpen$ = this.isOpen.asObservable();

  defaultConfig = {
    color: "#494a4f"
  } as ConfigItem;

  private configItem = new BehaviorSubject<string>('#494a4f');
  public configItem$ = this.configItem.asObservable();
  constructor() {}

  public setTotalItem(data: number) {
    if (data === this.totalItem.getValue()) {
      return;
    }

    if (data > 3) {
      this.totalItem.next(data);
    }
  }

  

  public addItemLoaded(data: number) {
    this.loadedItem.next(this.loadedItem.getValue() + data);

    const percentLoad = Math.ceil(
      (this.loadedItem.getValue() * 100) / this.totalItem.getValue()
    );
    this.percentLoading.next(percentLoad);
  }

  public setIsLoading(data: boolean) {
    this.isLoading.next(data);
  }
  public setPercentloaded(data: number) {
    this.percentLoading.next(data);
  }

  public close() {
    this.isOpen.next(false);
  }
  public getHexColor() {
    const temp = this.configItem.getValue() as string;
    return temp.replace("#", "0x");
  }

  public setColor(color) {
   
    // console.log('NEXT11111111111', color);
    // this.cd.detectChanges();
    this.configItem.next(color);
  }

  public open() {
    this.isOpen.next(true);
  }

  public onToggle() {
    if (this.isOpen.getValue()) {
      this.close();
    } else {
      this.open();
    }
  }
}
